import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import '../styles/AdminPage.css';

class LoginPage extends Component {
  state = {
    login: '',
    password: '',
    isLoged: false
  }

  date = {
    login: 'cesar',
    password: '1234'
  }

  handleLogin = e => {
    this.setState({
      login: e.target.value
    })
  }

  handlePassword = e => {
    this.setState({
      password: e.target.value
    })
  }

  handleSubmit = e => {
    const { login, password } = this.state;
    e.preventDefault();
    if( login === this.date.login && password === this.date.password) {
      this.setState({
        isLoged: true
      })
    }
  }


  render(){
    const content = () => {
      return (
        <>
          <h1>Login Page</h1>
            <form className="admin-form" onSubmit={this.handleSubmit}>
              <input value={this.state.login} onChange={this.handleLogin} placeholder="login" type="text"/>
              <input value={this.state.password} onChange={this.handlePassword} placeholder="hasło" type="password"/>
              <button>Zaloguj się</button>
            </form>
        </>
      )
    };

    return (
      <>
        {this.state.isLoged ? <Redirect to='/admin' /> : content()}
      </>
    )
  }
}

export default LoginPage;