import React from 'react';
import { Link } from 'react-router-dom';

const Product = ({ id, image, title, price }) => {
  return (
    <div className="product">
      <Link to={`/product/${id}`}>
        <div className="img-container">
          <img src={image} alt={title}/>
        </div>
        <h2>{title}</h2>
        <span>{price} $</span>
      </Link>
    </div>
  )
}

export default Product;