import React, { Component } from 'react';
import { Prompt } from 'react-router-dom';
import '../styles/ContactPage.css';


class ContactPage extends Component {

  state = {
    textarea: ''
  }

  handleSubmit = e => {
    e.preventDefault();
    this.setState({
      textarea: ''
    })
  }

  handleTextarea = e => {
    this.setState({
      textarea: e.target.value
    })
  }

  render() {
    return (
      <>
        <h1>Contact</h1>
        <form className="contact-form" onSubmit={this.handleSubmit}>
          <textarea onChange={this.handleTextarea} value={this.state.textarea} placeholder="Wpisz wiadomość..."></textarea>
          <button>Wyślij</button>
        </form>
        <Prompt
          when={this.state.textarea.length ? true : false}
          message="Masz niewysłany formularz. Czy na pewno chcesz opuścić tę stronę?"
        />
      </>
    )
  }
}

export default ContactPage;