import React, { Component } from 'react';
import '../styles/ProductPage.css';

class ProductPage extends Component {

  state = {
    product: null,
    isLoaded: false
  }

  componentDidMount(){
    fetch(`https://fakestoreapi.com/products/${this.props.match.params.id}`)
    .then(response => response.json())
    .then(data => {
      this.setState({
        product: data,
        isLoaded: true
      })
    })
  }

  render(){
    const product = () => {
    const { title, image, price, description } = this.state.product;
      return (
        <div className="product-page">
          <img src={image} alt={title}/>
          <div className="procuct-details">
            <h1>{title}</h1>
            <p>{description}</p>
            <div className="price-wrapp">
              <span>{price} $</span>
              <button>Add to cart</button>
            </div>
          </div>
        </div>
      )
    }

    return (
      <>
        {this.state.isLoaded && product()}
      </>
    )
  }
}

export default ProductPage;