import React, { Component } from 'react';
import Post from '../components/Post';

import '../styles/HomePage.css';


class Home extends Component {
  state = {
    posts: [],
    users: [],
    isLoaded: false
  }

  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/posts')
    .then(response => response.json())
    .then(data => {
      const posts = data.slice(0, 4);
      this.setState({
        posts
      })
      this.fetchUsers();
    })
  }

  fetchUsers = () => {
    fetch('https://jsonplaceholder.typicode.com/users')
    .then(response => response.json())
    .then(data => {
      this.setState({
        users: data,
        isLoaded: true
      })
    })
  }

  render(){
    const postList = this.state.posts.map(post => <Post key={post.id} post={post} user={this.state.users.filter(user => user.id === post.userId)[0]} />);

    return (
      <>
        <h1>Home</h1>
        <div>{this.state.isLoaded && postList}</div>
      </>
    )
  }
}

export default Home;