import React, { Component } from 'react';
import Product from '../components/Product';

import '../styles/ProductsPage.css';

class ProductsPage extends Component {

  state = {
    products: [],
    isLoaded: false
  }

  componentDidMount(){
    fetch('https://fakestoreapi.com/products')
    .then(response => response.json())
    .then(data => {
      this.setState({
        products: data,
        isLoaded: true
      })
    })
  }

  render(){
    const productsList = this.state.products.map(product => <Product key={product.id} {...product} />)
    return (
      <>
        <h1>Products</h1>
        <div className='products-grid'>{this.state.isLoaded && productsList}</div>
      </>
    )
  }
}

export default ProductsPage;